@import '../../_variables.scss';
@import 'swiper/css';
.slide {
  position: relative;
  &__rating {
    position: absolute;
    top: 5%;
    left: 5%;
    padding: 5px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background-color: rgb(227, 77, 22);
    color: #fff;
    z-index: 10;
    @media (max-width: 450px) {
      padding: 3px;
      font-size: 12px;
    }
  }

  &__link {
    &:hover .slide__img img {
      filter: none;
      -webkit-filter: none;
    }
  }
  &__img {
    margin-bottom: 5px;
    overflow: hidden;
    // width: 270px;
    height: 370px;
    // @media (min-width: 430px) {
    //   width: 190px;
    //   height: 280px;
    // }
    // @media (max-width: 600px) {
    //   width: 140px;
    //   height: 170px;
    // }
    @media (max-width: 450px) {
      height: 270px;
    }
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      filter: invert(10%);
      -webkit-filter: invert(10%);
      -moz-filter: invert(10%);
      border-radius: 3%;
      -webkit-border-radius: 3%;
      -moz-border-radius: 3%;
      -ms-border-radius: 3%;
      -o-border-radius: 3%;
      transition: filter 0.3s;
      -webkit-transition: filter 0.3s;
      -moz-transition: filter 0.3s;
      -ms-transition: filter 0.3s;
      -o-transition: filter 0.3s;
    }
  }
  &__title {
    font-weight: 500;
    color: #000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 5px;
  }
  &__text {
    color: #000;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
@import '../../_variables';
.btnAdd {
  color: #000;
  cursor: pointer;
  font-size: 18px;
  background: none;
  &:hover::after {
    content: attr(data-title);
    position: absolute;
    left: 20%;
    bottom: 0%;
    z-index: 1;
    background: rgba(245, 245, 245, 0.9);
    font-size: 10px;
    padding: 5px 10px;
    border: 1px solid #333;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
  }
}
